/**
 * Donation form base block
 */
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;
const { Tooltip, Button, Toolbar, PanelBody, IconButton, TextControl, BaseControl, MediaUpload } = wp.components;
const { InnerBlocks, RichText, InspectorControls, BlockControls} = wp.blockEditor;

registerBlockType('marlin/resources-icon', {
	title: __('Resources Block'),
	icon: 'image-flip-horizontal',
	category: 'common',
	keywords: [
		__('Resources Block')
	],

  attributes: require("./attributes.json"),
	edit: ({ className, attributes, setAttributes }) => {
		return <div className={className}>
			<section class="resources-icon-section sm:mb-0 pt-12 lg:mb-0">
			    <div class="flex flex-wrap">
			        <div class="resources-wrapper text-center w-full column-1 lg:px-36 lg:py-10 sm:px-24 sm:py-5">	

			            <div class="resources-container p-2">
			            	<InnerBlocks allowedBlocks={['marlin/resources-icon-item']} />
			        	</div>			            
			        </div>
			    </div>
			</section>
		</div>
	},

	save: () => <InnerBlocks.Content />

});
