/**
 * Hero Banner Image Block
 */
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;
const { PanelBody, IconButton, TextControl } = wp.components;
const {} = wp.blockEditor;

import { OptionSidebar } from '../OptionSidebar'

registerBlockType('marlin/hero-banner', {
	title: __('Hero Banner'),
	icon: 'image-flip-horizontal',
	category: 'common',
	keywords: [
		__('Hero Banner')
	],

	attributes: require('./attributes.json'),
	
	edit: ({ className, attributes, setAttributes }) => {
	const { title, body, hrefText } = attributes;
	const optionSchema = require('./options.json');
		return ( 
			<>
		<OptionSidebar
          attributes={attributes}
          setAttributes={setAttributes}
          optionSchema={optionSchema}
        ></OptionSidebar>
        <PanelBody title={__("Background options")}>
          <TextControl
            placeholder={__("Banner Caption")}
            value={title}
            onChange={(title) => setAttributes({ title })}
          />
        </PanelBody>
      </>
      );
	},
	save: ({ attributes, className }) => null
});