/**
 * Donation form base block
 */
import { OptionSidebar } from "../OptionSidebar";
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;
const { Tooltip, Button, Toolbar, PanelBody, IconButton, TextControl, TextareaControl } = wp.components;
const { InnerBlocks, RichText, InspectorControls, BlockControls, MediaUpload } = wp.blockEditor;

registerBlockType('marlin/resources-icon-item', {
	title: __('Resources icon item'),
	icon: 'image-flip-horizontal',
	category: 'common',
	keywords: [
		__('Resources icon item')
	],

	attributes: require('./attributes.json'),

	parent: [
		'marlin/resources-icon'
	],


	edit: ({ className, attributes, setAttributes }) => {
		const optionSchema = require("./options.json");
		function selectImage(value) {
			setAttributes({
				resources_icon_image: value.sizes.full.url,
			})
		}
		return <div className={className}>
					<div class="resources-item text-left w-full md:w-full lg:w-3/4 pt-10" data-attr="">
						<div class="resources-item-image">				
							<MediaUpload
								onSelect={selectImage}
								render={({ open }) => {
									return (
										<button onClick={open}>
											<img
												src={attributes.resources_icon_image}
											/>
										</button>
									);
								}}
							/>					
						</div>
						<div class="resources-title">
							<span><TextControl value={attributes.resources_text} placeholder="Resource Title"
						            				onChange={val => setAttributes({ resources_text:val })}
						            />
						     </span>
						</div>
						<div class="resources-link">
							<TextControl value={attributes.resources_link} placeholder='Link'
					            				onChange={val => setAttributes({ resources_link:val })}
					            />
						</div>
					</div>
				</div>
	},

	save: ({ attributes, className }) => null
});
