/**
 * Donation form base block
 */
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;
const { Tooltip, Button, Toolbar, PanelBody, IconButton, TextControl, TextareaControl } = wp.components;
const { InnerBlocks, RichText, InspectorControls, BlockControls, MediaUpload } = wp.blockEditor;

registerBlockType('marlin/latest-event-scroller-item', {
	title: __('Latest event scroller item'),
	icon: 'image-flip-horizontal',
	category: 'common',
	keywords: [
		__('Latest event scroller item')
	],

	attributes: require('./attributes.json'),

	parent: [
		'marlin/latest-event-scroller'
	],


	edit: ({ className, attributes, setAttributes }) => {
			function selectImage(value) {
		    console.log(value);
		    setAttributes({
		        event_img: value.sizes.full.url,
		    })
			}
		return <div className={className}>
				  <div class="events-wrap md:px-10 flex flex-wrap  lg:flex-no-wrap">
                     <div class="events-slide-image w-full md:w-full lg:w-1/4 pr-10">
						<MediaUpload 
						    onSelect={selectImage}
						    render={ ({open}) => {
						        return (
						            <button onClick={open}>
						                <img 
						                    src={attributes.event_img}
						                    />
						            </button>
						        );
						    }}
						/>

                     </div>  
                     <div class="events-slide-post text-left  w-full md:w-full lg:w-3/4 pt-10">
                        <div class="post-title">

                            <h3 class="font-extrabold text-2xl">

                            <TextControl value={attributes.event_title} placeholder="Event Title"
			            				onChange={val => setAttributes({ event_title:val })}
			            /></h3>
                            <p class="event-date pb-2 text-2xl"><TextControl value={attributes.event_date} placeholder="Event Date"
			            				onChange={val => setAttributes({ event_date:val })} /></p>
                        </div>
                        <div class="event-desription">
                            <p><TextareaControl value={attributes.event_desc} placeholder="Event Description"
			            				onChange={val => setAttributes({ event_desc:val })} /></p>
                        </div>
                        <div class="event-category object-none object-left-bottom">
                            <p class="video"><i class="fa fa-play-circle"></i> Video</p>
                        </div>
                     </div>    
               </div>                 
		</div>
	},

	save: ({ attributes, className }) => null
});
